@import '../../vars.scss';

.footer {
  width: 100%;
  background-color: #212529;
  color: white;

  .main {
    display: flex;
    justify-content: space-between;
    padding: 80px 0;
    border-bottom: 1px solid #F2F2F2;

    @media (max-width: $tablet-width) {
      padding: 56px 0;
    }

    @media (max-width: $mobile-width) {
      flex-direction: column;
      align-items: center;
    }

    .logoWrapper {
      box-sizing: border-box;
      padding-left: 112px;

      @media (max-width: $tablet-width) {
        padding-left: 48px;
      }

      @media (max-width: $mobile-width) {
        display: flex;
        justify-content: center;
        width: 100%;
        padding: 0;
      }
    }

    .menuList {
      display: flex;
      margin-right: 104px;

      @media (max-width: $tablet-width) {
        margin-right: 32px;
      }

      @media (max-width: $mobile-width) {
        flex-wrap: wrap;
        box-sizing: border-box;
        margin-top: 16px;
        margin-right: 0px;
        padding: 0 16px;
        width: 100%;
      }

      .menu {
        margin-right: 96px;

        @media (max-width: $tablet-width) {
          margin-right: 5.65vw;
        }

        @media (max-width: $mobile-width) {
          margin-right: 0px;
          margin-top: 40px;
          flex: 1;

          &:first-child {
            flex: 1.25;
          }
        }

        .title {
          font-weight: 500;
          font-size: 24px;
          line-height: 32px;
        }

        .links {
          display: flex;
          flex-direction: column;

          > div {
            margin-top: 16px;
          }
        }
      }
    }
  }

  .bottom {
    display: flex;
    padding: 40px 112px;
    font-size: 16px;
    line-height: 24px;

    @media (max-width: $tablet-width) {
      padding: 72px 38px 38px 48px;
    }

    @media (max-width: $mobile-width) {
      flex-direction: column;
      padding: 48px 16px 40px 16px;
    }

    .copyright {
      margin-right: 80px;

      @media (max-width: $tablet-width) {
        margin-right: 32px;
      }

      @media (max-width: $mobile-width) {
        margin-right: 0px;
        margin-bottom: 24px;
      }
    }

    .madeIn {
      svg {
        fill: white;
        opacity: 0.4;
      }
    }

    .social {
      display: flex;
      align-items: center;
      margin-left: auto;

      @media (max-width: $mobile-width) {
        margin-top: -24px;

        .text {
          display: none;
        }
      }

      svg {
        position: relative;
        top: 2px;
        fill: white;
        width: 16px;
        height: 16px;

        @media (max-width: $mobile-width) {
          top: 0px;
          width: auto;
          height: auto;
        }
      }

      .text,
      a {
        margin-right: 16px;
        color: white;

        &:last-child {
          margin-right: 0px;
        }
      }
    }
  }
}
